*, html {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    .container {
        max-width: 1520px;
    }
}
a {
    text-decoration: none;
}
input:focus {
    outline: 0;
}

body {
    font-family: 'Montserrat Alternates', sans-serif;
    position: relative;
}

html::-webkit-scrollbar-thumb {
    background: #1A6EC1;
}
html::-webkit-scrollbar-track {
    background: #fff;
}
html::-webkit-scrollbar {
    width: 15px;
}

.black-title {
    font-size: 52px;
    font-weight: 700;
    color: #000;
    text-align: center;
    margin-bottom: 50px;
}


.decor-1 {
    background-image: url(../images/decor/decor-1.png);
    background-repeat: no-repeat;
    background-size: 20%;
    background-position: left center;
}
.decor-2 {
    background-image: url(../images/decor/decor-2.png);
    background-repeat: no-repeat;
    background-size: 10%;
    background-position: right center;
}

@media screen and (max-width: 1400px) {
    .black-title {
        font-size: 36px;
        font-weight: 700;
        color: #000;
        text-align: center;
        margin-bottom: 40px;
    }
}
@media screen and (max-width: 1200px) {
    .black-title {
        font-size: 32px;
        font-weight: 700;
        color: #000;
        text-align: center;
        margin-bottom: 30px;
    }
}
@media screen and (max-width: 992px) {
    .black-title {
        font-size: 28px;
        font-weight: 700;
        color: #000;
        text-align: center;
        margin-bottom: 20px;
    }
}


.rating-result {
	width: 265px;
    display: flex;
    align-items: baseline;
}
.rating-result span {
	padding: 0;
	font-size: 32px;
	margin: 0 3px;
	// line-height: 1;
	color: lightgrey;
	text-shadow: 1px 1px #1A6EC1;
}
.rating-result > span:before {
	content: 'â˜…';
}
.rating-result > span.active {
	color: #1A6EC1;
	text-shadow: 1px 1px #1A6EC1;
}

@media screen and (max-width: 576px) {
    .rating-result span {
        font-size: 26px;

    }
}


.search-result {
    padding: 50px 0 50px;
    p {
        font-size: 20px;
        font-weight: 500;
    }
}

.no_scroll{
    overflow: hidden;
  }
